<template>
    <div>
        <h1  style="margin-left: 500px;"> </h1>
        <div>
            <my-input placeholder="   " v-model="NumOfPoints" @input="this.click = false"></my-input>
        </div>
        <div style="width: 300px; margin-left: auto;margin-right: auto; color: red" v-if="!validateNum">
                 !
        </div>
        <div>
            <my-input placeholder=" " v-model="Name" @input="this.click = false"   ></my-input>
        </div>
        <div style="width: 300px; margin-left: auto;margin-right: auto; color: red" v-if="!validateName">
                
        </div>

        <div>
            <my-button @click="registration()"></my-button>
        </div>
        <div>
            <div>
                <my-input class="search" placeholder=" " v-model="SearchFacility"></my-input>
            </div>
            <div>
                <facility-list :facilities="sortedAndSearchedPosts" @chooseFacility="facilityChoose($event)"></facility-list>
            </div>
        </div>
    </div>
</template>

<script>
    import PostsService from "@/service/PostsService"
    import FacilityList from "@/components/FacilityList";

    export default {
        components: {FacilityList},
        data(){
            return{
                Name: "",
                NumOfPoints: "",
                NumOfFacilities: "",
                WorkshopId: "",
                SearchFacility: "",
                Facilities: [],
                click: false
            }
        },
        methods: {
            async registration(){
                this.click = true
                if (!this.validateName || !this.validateNum || this.WorkshopId === ''){
                    return
                }
                try {
                    // let arr = [];
                    // for (let i = 0; i < this.NumOfFacilities; i++){
                    //     arr.push(PostsService.postFacility({num: this.NumOfPoints, workshopId: this.WorkshopId, name: this.Name}))
                    // }
                    // const response = await Promise.all(arr)
                    const response = await PostsService.postFacility({num: this.NumOfPoints, workshopId: this.WorkshopId, name: this.Name})
                    .then(p => this.getWorkshops())
                } catch (e) {
                    console.log(e.response?.data?.message)
                }
            },
            async getWorkshops(){
                try {
                    const response = await PostsService.getFacilities()
                    this.Facilities = response.data
                } catch (e) {
                    console.log(e.response?.data?.message)
                }
            },
            facilityChoose  (e){
                this.$router.push({ name:"tasks", params: { facilityId: e } })
            },

        },
        mounted() {
                this.WorkshopId = this.$route.params.workshopId;
                if (this.WorkshopId == undefined){
                    this.$router.push({ name: 'workshop' })
                }
                this.getWorkshops();
        },
        computed: {
            sortedAndSearchedPosts() {
                if (this.WorkshopId === undefined){
                    return [...this.Facilities].filter(facility => facility.name.toLowerCase().includes(this.SearchFacility.toLowerCase()))
                }
                return [...this.Facilities].filter(facility => facility.name.toLowerCase().includes(this.SearchFacility.toLowerCase()) && facility.workshop.id == this.WorkshopId)
            },
            validateNum(){
                const nums = "0123456789"
                if (!this.NumOfPoints){
                    return !this.click
                }
                for (let i = 0; i < this.NumOfPoints.length; i++) {
                    if (!nums.includes(this.NumOfPoints.charAt(i))){
                        return !this.click
                    }
                }

                return true
            },
            validateName(){
                if (this.Name === ''){
                    return !this.click
                }
                return true
            }
        }

    }
</script>

<style scoped>
.search{
    width: 80%;
    margin-top: 30px;
    margin-bottom: 30px;
}
</style>