<template>
        <div class="Post" @click="gradeChoose">
            <div class="line">: {{ grades.find(p => p.value == grade.rating).text }}</div>
            <div class="line">: {{ grade.teacher.name }}</div>
            <div class="line">: {{ grade.teacher.name }}</div>
        </div>
</template>

<script>
    export default {
        name: "grade",
        data(){
            return{
                grades: [
                    {text: "", value: "1"},
                    {text: "", value: "2"},
                    {text: "", value: "3"},
                    {text: "", value: "4"},
                    {text: "", value: "5"},
                ],
            }
        },
        props: {
            grade: {
                type: Object,
                required: true
            },
        },
        methods: {
            gradeChoose(){
                this.$emit("gradeChoose", this.task)
            }
        },

    }
</script>

<style scoped>
    .line{
        margin-bottom: 5px;
    }
    .Post{
        min-width: 200px;
        padding: 10px 15px;
        width: 24%;
        border: 2px solid #e9967a;
        margin: 5px;
    }

    .Post:hover{
        background-color: #f6cbba;
    }

</style>