<template>
    <div class="wrap">
        <div>
            <div>
                <my-input class="search" placeholder="" v-model="SearchStudent"></my-input>
            </div>

            <div class="rr">
                <student-list :students="SortedStudents" @chooseStudent="chooseStudent($event)"></student-list>
            </div>
        </div>

        <my-dialog v-model:show="showStuds">
            <h1> </h1>
            <div class="dialog"> : {{ `${this.user.name} ${this.user.surname} ${this.user.secondname}` }}</div>
            <div class="dialog"> : {{ `${this.task.description}` }}</div>
            <my-select :options="options" v-model="chosen">

            </my-select>
            <my-button @click="change" style="margin-top: 15px;"> </my-button>
        </my-dialog>

        <my-dialog v-model:show="showTasks">
            <h1> </h1>
            <div v-if="Tasks.length === 0">
                         
            </div>
            <subscribe-list :subscribes="Tasks" @unsubscribe="taskChose($event)"></subscribe-list>
<!--            <tasks-list :tasks="Tasks" @taskChose="taskChose($event)" class="newClass"></tasks-list>-->
        </my-dialog>
        <div v-if="IsError">
            : {{errorCode}}
        </div>
    </div>
</template>

<script>
    import UserService from "@/service/UserService";
    import StudentList from "@/components/StudentList";
    import MyInput from "@/components/UI/MyInput";
    import PostsService from "@/service/PostsService";
    import SubscribeList from "@/components/SubscribeList";

    export default {
        components: {SubscribeList, MyInput, StudentList},
        data(){
            return{
                students: [],
                showStuds: false,
                showTasks: false,
                user: {},
                chosenUser: {},
                task: {},
                chosenUserId: null,
                chosenTaskId: null,
                options: [
                    {text: "", value: "1"},
                    {text: "", value: "2"},
                    {text: "", value: "3"},
                    {text: "", value: "4"},
                    {text: "", value: "5"},
                ],
                chosen: '1',
                SearchStudent: '',
                Tasks: [],
                errorCode: '',
                loader: false
            }
        },
        mounted() {
            this.user = this.$store.getters['registration/User'];
            this.getStudents();
        },
        methods: {
            async getStudents(){
                try {
                    const response = await UserService.getAll();
                    this.students = response.data;
                } catch (e) {
                    console.log(e.response?.data?.message);
                }
            },

            async change(){
                try {
                    const grade = {
                        rating: Number.parseInt(this.chosen),
                        teacherId: this.user.id,
                        labUserId: this.chosenTaskId
                    }
                    const request = {
                        labId: this.chosenTaskId,
                        grade: grade
                    }
                    const response = await UserService.estimate(request)
                } catch (e) {
                    console.log(e.response?.data?.message);
                }
            },
            async chooseStudent(e){
                this.showTasks = true;
                this.chosenUserId = e;
                this.chosenUser = this.students.find(p => p.id === e)

                try {
                    this.loader = true;
                    const response = await PostsService.getUserSubscribes(e)
                    // this.Tasks = response.data.filter(p => p.day == new Date().getDay())
                    const a = response.data.find(p => p.day == new Date().getDay())
                    // this.Tasks = response.data.filter(p => new Date(p.date.split('T')[0]).getTime() === new Date('2022-07-04').getTime())
                    this.Tasks = response.data.filter(p => this.compareDate(p.date))
                }
                catch (e) {
                    this.errorCode = e
                    console.log(e?.data)
                }
                finally {
                    this.loader = false
                }

                this.user = this.$store.getters['registration/User'];
            },
            compareDate(str){
                const date = new Date(str)
                const year = date.getFullYear()
                const month = date.getMonth()
                const day = date.getDate()

                const today = new Date()

                if (year === today.getFullYear() && month === today.getMonth() + 1 && day === today.getDate()){
                    return true
                }
                return false
            },
            taskChose(e){
                this.task = this.Tasks.find(p => p.id === e).task
                this.chosenTaskId = e
                this.showStuds = true;
                this.showTasks = false;
            },
            setError (errorCode) {
                this.errorCode = errorCode;
            },
            clearError () {
                this.errorCode = '';
            }
        },
        computed: {
            SortedStudents() {
                return [...this.students]
                    .filter(s => s.role === "user")
                    .filter(student => `${student.name} ${student.surname} ${student.secondName}`.toLowerCase().includes(this.SearchStudent.toLowerCase()))
            },
            IsError(){
                return this.errorCode !== ''
            }
        }
    }
</script>

<style scoped>
    .dialog{
        margin: 10px;
    }

    .task__list{
        width: 90%;
    }

    .newClass{
        display: flex;
        flex-wrap: wrap;
    }
</style>