<template>
    <div>
        <h1  style="margin-left: 500px;"> </h1>
        <div>
            <my-select v-model="Day" :options="Days">

            </my-select>
        </div>
        <div>
            <my-input placeholder=" " v-model="TaskDescription"></my-input>
        </div>
        <div style="width: 300px; margin-left: auto;margin-right: auto; color: red" v-if="validate" @input="click = false">
                
        </div>
        <div>
            <my-button @click="registration()"></my-button>
        </div>
        <div>
            <div>
                <my-input class="search" placeholder=" " v-model="SearchTask"></my-input>
            </div>
            <div class="rr">
                <tasks-list :tasks="sortedAndSearchedPosts" @chooseFacility="chooseFacility($event)"></tasks-list>
            </div>
        </div>

    </div>
</template>

<script>
    import PostsService from "@/service/PostsService"
    import TasksList from "@/components/TasksList";

    export default {
        components: {TasksList},
        data(){
            return{
                Day: "",
                TaskDescription: "",
                NumOfFacilities: "",
                FacilityId: "",
                SearchTask: "",
                Tasks: [],
                Days: [
                    {text: "", value: ""},
                    {text: "", value: ""},
                    {text: "", value: ""},
                    {text: "", value: ""},
                    {text: "", value: ""},
                    {text: "", value: ""},
                    {text: "", value: ""},
                ],
                DayOfWeek: {
                    "" : 0,
                    "" : 1,
                    "" : 2,
                    "" : 3,
                    "" : 4,
                    "" : 5,
                    "" : 6
                }, click: false
            }
        },
        methods: {
            async registration(){
                this.click = true
                if (this.TaskDescription === '')
                {
                    return
                }
                try {
                    const responseLab = await PostsService.postLab({facilityId: this.FacilityId, day: this.DayOfWeek[this.Day]})
                    const labId = responseLab.data.id
                    await PostsService.postTask({description: this.TaskDescription, labId: labId})
                    await this.getTasks();
                } catch (e) {
                    console.log(e.response?.data?.message)
                }
            },
            async getTasks(){
                try {
                    const response = await PostsService.getTasks()
                    this.Tasks = response.data
                } catch (e) {
                    console.log(e.response?.data?.message)
                }
            },
            chooseFacility(){

            }
        },
        mounted() {
            this.FacilityId = this.$route.params.facilityId
            if (this.FacilityId == undefined){
                this.$router.push({ name: 'workshop' })
            }
            this.getTasks();
        },
        computed: {
            sortedAndSearchedPosts() {
                if (this.FacilityId === undefined){
                    return [...this.Tasks].filter(task => task.description.toLowerCase().includes(this.SearchTask.toLowerCase()))
                }
                return [...this.Tasks].filter(task => task.description.toLowerCase().includes(this.SearchTask.toLowerCase()) && task.facilityId == this.FacilityId)
            },
            validate(){
                return this.TaskDescription === '' && this.click
            }
        }

    }
</script>

<style scoped>
    .search{
        width: 80%;
        margin-top: 30px;
        margin-bottom: 30px;
    }
    .rr{
        width: 60%;
        margin-left: auto;
        margin-right: auto;
        text-align: center;
    }
</style>