<template id="app">
    <div id="nav">
        <div v-if="admin">
            <router-link to="/">Home</router-link>
            |
            <router-link to="/students"></router-link>
            |
            <router-link to="/workshop"> </router-link>
        </div>
        <div v-if="student">
            <router-link to="/">Home</router-link>
            |
            <router-link to="/registration">Registration</router-link>
            |
            <router-link to="/students"></router-link>
        </div>
        <div v-if="teacher">
            <router-link to="/">Home</router-link>
            |
            <router-link to="/grades"></router-link>
            |
            <router-link to="/students"></router-link>
        </div>


        <div class="profile">
            <my-button class="element" @click="goToProfile">
                
            </my-button>

            <my-button class="element" @click="logoutA">
                
            </my-button>
        </div>
    </div>
    <router-view/>
</template>

<script>
    import {mapActions} from "vuex";

    export default {
        methods: {
            data() {
                return {
                    user: {},
                }

            },
            ...mapActions({
                logout: 'registration/logout',
            }),
            goToProfile() {
                this.$router.push({name: 'profile'})
            },
            logoutA() {
                this.logout()
                this.$router.push({name: 'Registration'})
            }
        },
        mounted() {
            const link = this.$route.params.link
            if (link != null) {
                this.activate({link: link})
            }
            this.user = this.$store.getters['registration/User'];
        },
        computed: {
            admin() {
                const user = this.$store.getters['registration/User'];

                if (user === null) {
                    return false
                }
                return user.role === 'admin'
            },
            student() {
                const user = this.$store.getters['registration/User'];
                if (user === null) {
                    return false
                }
                return user.role === 'user'
            },
            teacher() {
                const user = this.$store.getters['registration/User'];
                if (user === null) {
                    return false
                }
                return user.role === 'teacher'
            }

        }
    }
</script>

