<template>
    <div class="common">
        <div class="leftBar">
            <div class="sting">: {{ User.surname }}</div>
            <div class="sting">: {{ User.name }}</div>
            <div class="sting">: {{ User.secondname }}</div>
            <div class="sting">Email: {{ User.email }}</div>
            <div class="sting">: {{ User.role }}</div>
        </div>
        <div class="supremum" v-if="User.role === 'user'">
            <div class="bottomBar">
                <div class="row"> </div>
                <subscribe-list :subscribes="subscribes" @unsubscribe="choose"></subscribe-list>
            </div>
            <div class="rightBar">
                <div class="row"> </div>
                <grade-list :grades="grades"></grade-list>
            </div>
        </div>
        <my-dialog v-model:show="show">
            <div class="text">
                  
            </div>
            <v-btn
                    color="grey"
            @click="unsubscribe">
                
            </v-btn>
        </my-dialog>
    </div>

</template>

<script>
    import PostsService from "@/service/PostsService";
    import UserService from "@/service/UserService";

    export default {
        data(){
            return{
                User: this.$store.getters['registration/User'],
                subscribes: [],
                grades: [],
                show: false,
                chosenTask: null
            }
        },
        mounted() {
                this.getSubscribes()
                this.getUser()
                this.getGrades()
        },
        methods: {
            async choose(e){
                this.chosenTask = e
                this.show = true
            },
            async unsubscribe(){
                try {
                    const resp = await PostsService.unsubscribe(this.chosenTask)
                }
                catch (e) {
                    console.log(e)
                }
                const index = this.subscribes.findIndex((x) => x.id === this.chosenTask)
                this.subscribes.splice(index, 1)
            },
            async getSubscribes(){
                try {
                    await PostsService.getUserSubscribes(this.User.id).then(p =>
                        {
                            this.subscribes = p.data
                        }
                    )
                }
                catch (e) {
                    console.log(e)
                }
            },
            async getGrades(){
                try {
                    await PostsService.getGrades(this.User.id).then(p =>
                        {
                            this.grades = p.data
                        }
                    )
                }
                catch (e) {
                    console.log(e)
                }
            },
            getUser(){
                this.$store.dispatch('registration/updateUser')
            }
        }
    }
</script>

<style scoped>
.leftBar{
    top: 50px;
    right: 50px;
    border: #2c3e50 solid;
    width: 40%;
    height: 50% ;
    padding: 10px;

}

.sting{
    text-align: left;
    margin-bottom: 5px;
}
.common{
    display: block;

}

.bottomBar{
    margin: 5px;
    border: #2c3e50 solid;
    width: 50%;
}

.rightBar{
    margin: 5px;
    border: #2c3e50 solid;
    width: 50%;
}
    .list{
        height: 100%;
    }

.row{
    border: #2c3e50 solid 1px;
}
.supremum{
    display: flex;
    justify-content: space-around;
}
.text{
    margin: 10px;
}
</style>